body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#headshot {
  border-radius: 50%;
  width: 80px;
  height: 80px;
  object-fit: cover;
  object-position: 100% 12%;
  margin-top: 20px;
  margin-left: 5px;
  float: left;
  margin-right: 20px;
  -webkit-shape-outside: circle();
          shape-outside: circle();
}

#first {
	display: block;
	font-size: 70px;
	text-transform: uppercase;
	font-family: Helvetica;
	margin: 0;
	line-height: 85%;
	font-weight: bolder;
	margin-left: 7px;
}

#second {
	margin: 0;
	font-size: 70px;
	text-transform: uppercase;
	font-family: Helvetica;
	line-height: 85%;
	font-weight: bolder;
}


#links {
	margin-top: 10%;
}

#personal-info-container {
	position: fixed;
	grid-area: info;
	z-index: 1;
	width: 100%;
	height: 100%;
	background-color: #F9FFFD;
}

#personal-info {
	position: relative;
	top: 10%;
	left: 3%;
}

#icons {
	margin-left: 115%;
	margin-top: 10px;
}

#statement {
	width: 600px;
	margin-top: 20px;
	font-size: 14.7px;
}

#info-github-icon {
	left: 130px;
}

#envelope-icon {
	left: 90px;
}

#envelope-icon:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1)
}

#info-github-icon:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

p {
	margin: 0;
}
	
#info-github-icon, #envelope-icon {
	color: black;
	transition: -webkit-transform .1s ease-in;
	transition: transform .1s ease-in;
	transition: transform .1s ease-in, -webkit-transform .1s ease-in;
	width: 20px;
	position: absolute;
}
#cardViewer {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	grid-area: cards;
	margin-top: 2%;
	margin-bottom: 40%;
}
.card {
	width: 90%;
	height: auto;
	border-radius: 2%;
	background-color: #E2DEDE;
	margin-top: 25%;
	position: relative;
	-webkit-filter: drop-shadow(5px 5px 5px rgb(0, 0, 0, .25));
	        filter: drop-shadow(5px 5px 5px rgb(0, 0, 0, .25));
	padding-bottom: 15%;
	padding-top: 5%;
	z-index: 1;
}

h1 {
	margin: 0;
	margin-bottom: 2px;
	font-size: 20px;
	font-weight: bold;
}

h2 {
	margin: 0;
	font-size: 15px;
	font-weight: lighter;
}

#project-title {
	position: absolute;
	width: 85%;
	bottom: 5%;
	left: 7%;
	font-size: 12px;
}

#screen {
	width: 85%;
	-webkit-perspective: 100px;
	        perspective: 100px;
	position: relative;
	top: 7%;
	left: 7.5%;
	-webkit-filter: drop-shadow(0 5px 5px gray);
	        filter: drop-shadow(0 5px 5px gray);
}

#github-icon {
	position: absolute;
	bottom: 0;
	right: 0;
	width: 25px;
	color: black;
}
.App {
	font-family: Helvetica;
	width: 100vw;
	height: 100vh;
}

@media only screen and (min-width: 1200px) {
	#first, #second {
		font-size: 80px;
	}

	#headshot {
		width: 80px;
		height: 80px;
	}

	#statement {
		font-size: 15.2px;
	}
}

@media only screen and (max-width: 1200px) {
	#first, #second {
		font-size: 60px;
	}

	#statement {
		font-size: 14px;
		width: 500px;
	}

	#headshot {
		width: 70px;
  		height: 70px;
	}

	#info-github-icon {
		left: 90px;
		margin-top: -7px;
	}

	#cardViewer h1 {
		font-size: 15px;
	}

	#cardViewer h2 {
		font-size: 12px;
	}

	#envelope-icon {
		left: 50px;
		margin-top: -7px;
	}
}

@media only screen and (min-width: 900px) {
	.App {
		display: grid;
		grid-template-columns: 60% 40%;
		grid-template-areas: "info cards";
	}
}


@media only screen and (max-width: 900px) {
	.App {
		display: grid;
		grid-template-rows: 30% 70%;
		grid-template-areas: 
			"info"
			"cards";
		align-items: center;
	}

	#cardViewer {
		margin-bottom: 5%;
		margin-top: 165%;
	}

	.card {
		margin-top: 25%;
	}

	#personal-info {
		position: absolute;
		top: 0;
		left: 50%;
        margin-right: -50%;
        -webkit-transform: translate(-50%, 0%);
                transform: translate(-50%, 0%);
		margin-left: 5px;
		margin-right: 5px;
	}

	#personal-info-container {
		background-color: #F9FFFD;
		border-bottom: 1px solid lightgray;
		height: 350px;
		z-index: 2;
	}

	#info-github-icon {
		margin-top: -10px;
	}

	#envelope-icon {
		margin-top: -10px;
	}
}

@media only screen and (max-width: 600px){

	#personal-info-container {
		height: 200px;
		width: 100%;
	}

	#first {
		margin-left: .5%;
	}

	#first, #second {
		font-size: 30px;
		margin-left: 20px;
	}

	#statement {
		margin-top: 15px;
		font-size: 9.5px;
		width: 330px;
	}

	#info-github-icon {
		position: absolute;
		left: 85px;
		width: 15px;
	}

	#envelope-icon {
		position: absolute;
		left: 60px;
		width: 15px;
	}

	#headshot {
		margin-top: 15px;
		margin-left: 15px;
		width: 50px;
  		height: 50px;
	}
}
